/** @format */

import type {
  LoadResult,
  PartialResolvedId,
  SourceDescription,
  PluginContext as RollupPluginContext,
  ResolvedId,
} from 'rollup'
import { Plugin } from './plugin'

export interface PluginContainer {
  resolveId(id: string, importer?: string): Promise<PartialResolvedId | null>
  load(id: string): Promise<LoadResult | null>
  transform(code: string, id: string): Promise<SourceDescription | null>
}

// æ¨¡æ‹Ÿ Rollup çš„æ’ä»¶æœºåˆ¶
export const createPluginContainer = (plugins: Plugin[]): PluginContainer => {
  // æ’ä»¶ä¸Šä¸‹æ–‡å¯¹è±¡
  // @ts-ignore è¿™é‡Œä»…å®ç°ä¸Šä¸‹æ–‡å¯¹è±¡çš„ resolve æ–¹æ³•
  class Context implements RollupPluginContext {
    async resolve(id: string, importer?: string) {
      let out = await pluginContainer.resolveId(id, importer)
      if (typeof out === 'string') out = { id: out }
      return out as ResolvedId | null
    }
  }
  // æ’ä»¶å®¹å™¨ä¸»è¦å°±æ˜¯ä¸ºäº†å¯¹äºæ’ä»¶åšè§£æ
  const pluginContainer: PluginContainer = {
    /**
     * å°è¯•è§£æç»™å®šçš„æ¨¡å— IDï¼Œå¹¶åœ¨è§£ææˆåŠŸæ—¶è¿”å›è§£æåçš„ ID
     *
     * è¿™ä¸ªå‡½æ•°æ¥å—ä¸¤ä¸ªå‚æ•°ï¼šID å’Œå¯é€‰çš„å¯¼å…¥å™¨å­—ç¬¦ä¸²ã€‚å®ƒéå†ä¸€ä¸ªæ’ä»¶æ•°ç»„ï¼Œå¯»æ‰¾å…·æœ‰ resolveId æ–¹æ³•çš„æ’ä»¶ã€‚
     * å¦‚æœæ‰¾åˆ°è¿™æ ·çš„æ’ä»¶ï¼Œå®ƒå°†è°ƒç”¨è¯¥æ’ä»¶çš„ resolveId æ–¹æ³•ï¼Œå¹¶ä¼ å…¥ ID å’Œå¯¼å…¥å™¨ä½œä¸ºå‚æ•°ã€‚è¿™ä¸ªæ–¹æ³•å¯ä»¥åŒæ­¥æˆ–å¼‚æ­¥åœ°è¿”å›ä¸€ä¸ªæ–°çš„ IDï¼Œè¿™ä¸ª ID å¯ä»¥æ˜¯å­—ç¬¦ä¸²æˆ–è€…å…·æœ‰ id å±æ€§çš„å¯¹è±¡ã€‚
     * å¦‚æœè¿”å›äº†æ–°çš„ IDï¼Œå®ƒå°†è¢«èµ‹å€¼ç»™å‡½æ•°çš„å±€éƒ¨å˜é‡ idï¼Œå¹¶ä½¿ç”¨è¿™ä¸ªæ–° ID è¿”å›ä¸€ä¸ªåŒ…å« id çš„å¯¹è±¡ã€‚å¦‚æœæ²¡æœ‰æ’ä»¶èƒ½å¤Ÿè§£æ IDï¼Œå‡½æ•°å°†è¿”å› nullã€‚
     *
     * @param {string} id - è¦è§£æçš„æ¨¡å— ID
     * @param {string=} importer - å¯é€‰çš„å¯¼å…¥å™¨å­—ç¬¦ä¸²ï¼Œç”¨äºä¼ é€’ç»™æ’ä»¶
     * @returns {Promise<{ id: string } | null>} - ä¸€ä¸ª Promiseï¼Œè§£ææˆåŠŸæ—¶è¿”å›ä¸€ä¸ªåŒ…å«è§£æå ID çš„å¯¹è±¡ï¼Œå¦åˆ™è¿”å› null
     */
    async resolveId(id: string, importer?: string) {
      const ctx = new Context() as any
      // éå†æ‰€æœ‰çš„æ’ä»¶ï¼ŒçŸ¥é“æ‰¾åˆ°ä¸€ä¸ªåŒ¹é…çš„æ’ä»¶ï¼Œæ‹¿åˆ°æ‹¼æ¥åçš„ id ç»“æœä¸ºæ­¢è¿”å›
      // å…¶å®è°ƒç”¨çš„éƒ½æ˜¯æ’ä»¶ä¸­çš„resolveId æ–¹æ³•
      for (const plugin of plugins) {
        if (plugin.resolveId) {
          const newId = await plugin.resolveId.call(ctx as any, id, importer)
          if (newId) {
            id = typeof newId === 'string' ? newId : newId.id
            return { id }
          }
        }
      }
      return null
    },
    async load(id) {
      const ctx = new Context() as any
      for (const plugin of plugins) {
        // å…¶å®ç¼–è¯‘ä¹Ÿæ˜¯æ‹¿åˆ°æ’ä»¶ä¸­å¯¹é¥®çš„æ–¹æ³•å»ç¼–è¯‘
        // è¿™é‡Œå°±å°†ä¸»ä½“æµç¨‹å’Œæ’ä»¶çš„èƒ½åŠ›åšåˆ°äº†é«˜åº¦è§£è€¦
        if (plugin.load) {
          const result = await plugin.load.call(ctx, id)
          if (result) {
            return result
          }
        }
      }
      return null
    },
    async transform(code, id) {
      const ctx = new Context() as any
      for (const plugin of plugins) {
        if (plugin.transform) {
          const result = await plugin.transform.call(ctx, code, id)
          if (!result) continue
          if (typeof result === 'string') {
            code = result
          } else if (result.code) {
            code = result.code
          }
        }
      }
      return { code }
    },
  }

  return pluginContainer
}
