import { NextHandleFunction } from 'connect'
import { CLIENT_PUBLIC_PATH } from '../../constants'
import {
  isJSRequest,
  isCSSRequest,
  isImportRequest,
  isVue,
  isInternalRequest,
  cleanUrl,
} from '../../utils'
import { ServerContext } from '../index'
import createDebug from 'debug'

const debug = createDebug('dev')

export async function transformRequest(
  url: string,
  serverContext: ServerContext,
) {
  const { moduleGraph, pluginContainer } = serverContext
  // å¯¹ url è¿›è¡Œæ¸…æ´—
  url = cleanUrl(url)
  // é€šè¿‡url æ˜ å°„æ‹¿åˆ°ç¼–è¯‘åçš„å®ä¾‹
  let mod = await moduleGraph.getModuleByUrl(url)
  // å¦‚æœå·²ç»ç¼–è¯‘è¿‡äº†ï¼Œå°±è¿”å›ç»“æœ
  if (mod && mod.transformResult) {
    return mod.transformResult
  }
  // ä¸ç„¶å°±è¦é‡æ–°ç¼–è¯‘
  // åˆ†åˆ«ä»æ’ä»¶ä¸­æ‹¿åˆ°å¯¹åº”çš„ id çƒ­æ›´æ–°çš„æ—¶å€™ç¼“å­˜çš„æ—¶å€™åº”è¯¥ä¹Ÿéœ€è¦è¿™ä¸ª id
  const resolvedResult = await pluginContainer.resolveId(url)
  let transformResult
  // å¯åŠ¨ç¼–è¯‘ï¼Œæ‹¿åˆ°ä»£ç 
  if (resolvedResult?.id) {
    // ä»æ–‡ä»¶ä¸­æ‹¿åˆ°æºç 
    let code = await pluginContainer.load(resolvedResult.id)
    // æœ‰å¯èƒ½ä¸€æ¬¡å¤„ç†å¥½äº†ï¼Œ å°±ä¸ç”¨å†ç¼–è¯‘äº†ç›´æ¥è¿”å›å¥½å°±è¡Œï¼Œæ¯”å¦‚é™æ€èµ„æº
    if (typeof code === 'object' && code !== null) {
      code = code.code
    }
    // è¿™ä¸ªæ˜¯æœ‰å¯èƒ½æœ‰ mod å®ä¾‹äº†ï¼Œä½†æ˜¯è¿˜æ²¡æœ‰ç¼–è¯‘ï¼Œæ‰€ä»¥è¦åœ¨è·å–ä¸€æ¬¡
    mod = await moduleGraph.ensureEntryFromUrl(url)
    // å¦‚æœå·²ç»è¯»å–äº†æºæ–‡ä»¶äº†
    if (code) {
      // å¯åŠ¨ç¼–è¯‘æµç¨‹
      // å¥—è·¯è¿˜æ˜¯ä¸€æ ·ï¼Œéå†æ’ä»¶ç„¶åè°ƒç”¨æ’ä»¶ä¸­çš„æ–¹æ³•ï¼Œç¼–è¯‘è¿”å›ç»“æœ
      // resolvedResult?.id è¿™é‡Œçš„ id å¯¹äºèƒ½ç¡®å®šç¼–è¯‘çš„æ˜¯å“ªç§æ–‡ä»¶
      transformResult = await pluginContainer.transform(
        code as string,
        resolvedResult?.id,
      )
    }
  }
  // ç„¶åè®²ç»“æœä¿å­˜åˆ°å½“å‰ url å¯¹åº”çš„å®ä¾‹ä¸­ï¼Œæ–¹ä¾¿åç»­è°ƒç”¨
  if (mod) {
    mod.transformResult = transformResult
  }
  // è¿”å›ç¼–è¯‘ç»“æœ
  return transformResult
}

export function transformMiddleware(
  serverContext: ServerContext,
): NextHandleFunction {
  return async (req, res, next) => {
    // å¦‚æœè¯·æ±‚æ–¹æ³•ä¸æ˜¯ GET æˆ–è€…è¯·æ±‚ URL ä¸ºç©ºï¼Œåˆ™è·³è¿‡å½“å‰ä¸­é—´ä»¶ï¼Œäº¤ç»™ä¸‹ä¸€ä¸ªä¸­é—´ä»¶æˆ–è€…è·¯ç”±å¤„ç†å‡½æ•°
    // ç›¸å½“äºå…œåº•å¤„ç†
    if (req.method !== 'GET' || !req.url) {
      return next()
    }
    const url = req.url
    debug('transformMiddleware: %s', url)
    // transform JS and CSS request
    // åˆ¤æ–­æ˜¯å¦æ˜¯ js css,vue æˆ–è€…é™æ€èµ„æºç±»å‹
    if (
      isJSRequest(url) ||
      isVue(url) ||
      isCSSRequest(url) ||
      // é™æ€èµ„æºçš„ import è¯·æ±‚ï¼Œå¦‚ import logo from './logo.svg?import';
      isImportRequest(url)
    ) {
      // æ ¸å¿ƒå¤„ç†å‡½æ•° ä¼ å…¥ url å’Œä¸Šä¸‹æ–‡å¯¹è±¡
      let result = await transformRequest(url, serverContext)
      if (!result) {
        // å¦‚æœæ²¡æœ‰ç»“æœï¼Œé‚£ä¹ˆå°±äº¤ç»™ä¸‹ä¸€ä¸ªä¸­é—´ä»¶å¤„ç†
        return next()
      }
      // å¦‚æœæœ‰ï¼Œå¹¶ä¸”ä¸æ˜¯ä¸€ä¸ªå­—ç¬¦ä¸²ï¼Œæˆ‘ä»¬æ‹¿åˆ°ç¼–è¯‘ç»“æœå°±å¯ä»¥äº†
      // å› ä¸ºæœ‰çš„è¿”å›çš„å¯èƒ½æ˜¯ä¸€ä¸ªå¯¹è±¡
      if (result && typeof result !== 'string') {
        result = result.code
      }
      res.statusCode = 200
      res.setHeader('Content-Type', 'application/javascript')
      // è¿”å›ç»™å®¢æˆ·ç«¯,ä¸‹ä¸€ä¸ªä¸­é—´ä»¶å°±ä¸èµ°äº†
      return res.end(result)
    }
    // å¦‚æœä»€ä¹ˆéƒ½ä¸å‘½ä¸­ï¼Œç»™åŠ ä¸€ä¸ªä¸­é—´ä»¶å¤„ç†

    return next()
  }
}
