import { NextHandleFunction } from 'connect'
import { CLIENT_PUBLIC_PATH } from '../../constants'
import { isImportRequest } from '../../utils'
//ç”¨äºå¤„ç†é™æ€èµ„äº§è¯·æ±‚çš„ä¼˜åŒ–è½»é‡çº§ä¸­é—´ä»¶
import sirv from 'sirv'

export function staticMiddleware(root: string): NextHandleFunction {
  // è·å–é™æ€èµ„æº
  const root2 = root+'/public'
  const serveFromRoot = sirv(root, { dev: true })
  const serveFromRoot2 = sirv(root2, { dev: true })
  return async (req, res, next) => {
    // æœ€åä¸€ä¸ªäº†ï¼Œnext å°±ä¸ç”¨è°ƒç”¨äº†
    // å¦‚æœå•¥ä¹Ÿæ²¡æœ‰ï¼Œé‚£å°±ç›´æ¥è¿”å›
    if (!req.url) {
      return
    }

    // å¦‚æœæ˜¯ import èµ„æºï¼Œæˆ–è€…ï¼Œ"/@vite/client" èµ„æº ä¸å¤„ç†
    // ä¹‹æ‰€ä»¥ä¸å¤„ç† ?import èµ„æºï¼Œæ˜¯ç”±äºå¸¦ import æœ¬è´¨ä¸Šæ˜¯ä¸ªjs æ–‡ä»¶ï¼Œè€Œä¸æ˜¯ä¸ªé™æ€èµ„æº
    if (isImportRequest(req.url) || req.url === CLIENT_PUBLIC_PATH) {
      return
    }

    // æ­£å¼å¤„ç†é™æ€æ–‡ä»¶
    serveFromRoot(req, res, ()=>{
      serveFromRoot2(req, res, next);
    })
  }
}
